.staff {
  padding: 60px 0;
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 90px / 4));
    gap: 30px;
    margin-top: 30px;
  }
  &-card {
    &__img {
      position: relative;
      padding-bottom: 150%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
    &__desc {
      margin-top: 24px;
    }
    &__name {
      font-size: 24px;
      line-height: 130%;
      border-bottom: 4px solid rgba(104, 185, 0, 1);
      padding-bottom: 15px;
    }
    &__post {
      font-size: 14px;
      line-height: 130%;
      margin-top: 15px;
    }
  }
}

@media screen and (max-width: 992px) {
  .staff {
    padding: 30px 0 60px 0;
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
    }
    &-card {
      &__name {
        font-size: 18px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .staff {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 30px / 2));
      gap: 15px;
    }
  }
}

@media screen and (max-width: 576px) {
  .staff {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      margin-top: 15px;
    }
  }
}