h2 {
  font-size: 30px;
  line-height: 120%;
  font-weight: 300;
}
.city-tab__content {
  display: none;
  &--active {
    display: block;
  }
}
.language-content {
  display: none;
  &--active {
    display: block;
  }
}
.btn-blue {
  gap: 16px;
  height: 70px;
  background: #000795;
  filter: drop-shadow(4.815px 6.389px 0.5px rgba(0,0,0,0.28));
  border-radius: 16px;
  padding: 5px 15px;
  &:hover {
    background: #1f26e5;
  }
  span {
    font-size: 38px;
    line-height: 130%;
    color: #fff;
  }
  i svg {
    width: 40px;
    height: auto;
  }
  i svg path {
    fill: #ff7e16;
  }
}
.swiper-button-prev,
.swiper-button-next {
  transform: translate(0, -50%);
  width: auto;
  height: auto;
  margin-top: 0;
  &:after {
    width: 100%;
    font-size: 50px;
    color: #000795;
  }
}
.swiper-pagination {
  &-bullet {
    width: 22px;
    height: 22px;
    background: rgba(0, 7, 149, 1);
    border: 1px solid transparent;
    opacity: 1;
    margin: 0 8px !important;
  }
  &-bullet-active {
    background: #fff;
    border: 1px solid rgba(0, 7, 149, 1);
  }
}
.navigation {
  padding: 50px 0;
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li a {
      font-size: 24px;
      line-height: 130%;
      &:hover {
        color: #fcc105;
      }
    }
  }
}


@media screen and (max-width: 1200px) {
  .btn-blue {
    span {
      font-size: 28px;
    }
    i svg {
      width: 32px;
    }
  }
}

@media screen and (max-width: 992px) {
  h2 {
    font-size: 24px;
  }
  .btn-blue {
    height: 60px;
    gap: 8px;
    span {
      font-size: 18px;
    }
    i {
      width: 24px;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    &:after {
      font-size: 40px;
    }
  }
  .swiper-pagination {
    &-bullet {
      width: 14px;
      height: 14px;
      margin: 0 4px !important;
    }
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 20px;
  }
  .btn-blue {
    height: 50px;
    span {
      font-size: 14px;
    }
    i svg {
      width: 20px;
    }
  }
}