.footer {
  position: relative;
  z-index: 2;
  background: #000796;
  color: #fff;
  padding: 40px 0;
  overflow: hidden;
  &__img {
    position: absolute;
    top: -90px;
    left: 169px;
    transform: rotate(-25deg);
    width: 330px;
    opacity: 0.3;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img2 {
    position: absolute;
    left: 50%;
    bottom: 15px;
    transform: translate(-50%, 0) rotate(-25deg);
    width: 107px;
    opacity: 0.3;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img3 {
    position: absolute;
    right: 160px;
    bottom: -30px;
    transform: rotate(-25deg);
    width: 200px;
    opacity: 0.3;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.footer-top {
  position: relative;
  z-index: 2;
  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    gap: 10px 25px;
    li a {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #ff7e16;
      filter: drop-shadow(4.815px 6.389px 0.5px rgba(0,0,0,0.28));
      border-radius: 11px;
      padding: 10px;
      font-size: 24px;
      line-height: 130%;
    }
    li a:hover {
      background: #fff;
      color: #000795;
    }
    .active a {
      background: #fff;
      color: #000795;
    }
  }
  .language-content {
    display: none;
  }
  .language-content--active {
    display: flex;
  }
}
.footer-bottom {
  position: relative;
  z-index: 2;
  margin-top: 60px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__socials {
    display: flex;
    gap: 24px;
  }
  &__social {
    max-width: 32px;
    &:hover {
      opacity: 0.7;
    }
    img {
      max-width: 100%;
    }
  }
  &__text {
    display: flex;
    gap: 10px;
    p {
      font-size: 18px;
      line-height: 130%;
    }
    a {
      border-bottom: 1px solid #fff;
    }
    a:hover {
      color: #ff7e16;
      border-color: #ff7e16;
    }
  }
  .language-content {
    display: none;
  }
  .language-content--active {
    display: block;
  }
}

@media screen and (max-width: 1200px) {
  .footer {
    &__img {
      left: 16px;
    }
    &__img3 {
      right: 16px;
    }
  }
  .footer-top {
    ul {
      justify-content: start;
      gap: 10px;
      li a {
        font-size: 22px;
      }
    }
  }
}

@media screen and (max-width: 1150px) {
  .footer-top {
    ul {
      li a {
        font-size: 18px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__img {
      top: -30px;
      width: 200px;
    }
    &__img2 {
      width: 60px;
    }
    &__img3 {
      width: 120px;
    }
  }
  .footer-bottom {
    margin-top: 40px;
    &__text {
      p {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    ul {
      flex-direction: column;
      align-items: center;
      gap: 15px;
    }
  }
  .footer-bottom {
    &__inner {
      flex-direction: column;
      text-align: center;
    }
    &__left {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    padding: 30px 0;
    &__img {
      width: 150px;
    }
    &__img3 {
      bottom: -10px;
      width: 80px;
    }
  }
  .footer-top {
    ul {
      li a {
        font-size: 14px;
      }
    }
  }
  .footer-bottom {
    margin-top: 30px;
    &__text {
      p {
        font-size: 14px;
      }
    }
    &__socials {
      gap: 16px;
    }
    &__social {
      max-width: 24px;
    }
  }
}

