.section-group {
  padding: 60px 0 20px;
  &__inner {
    border: 8px solid #ff7e16;
    border-radius: 26px;
    overflow: hidden;
  }
}
.selection {
  background: #eaeff6;
  padding: 40px;
}
.services {
  background: #ff7e16;
  padding: 40px;
}
.connection {
  padding: 20px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 60px;
    background-color: #68b900;
    border-radius: 35px;
    padding: 40px;
  }
  &__left {
    max-width: 430px;
  }
  &__right {
    width: calc(100% - 490px);
  }
  &__text {
    font-size: 25px;
    line-height: 130%;
    color: #fff;
  }
  .form {
    width: 100%;
    display: flex;
    align-items: center;
    gap: 40px;
    &-group {
      width: 50%;
      border: unset;
      padding: 0;
      margin: 0;
      input {
        width: 100%;
        height: 70px;
        background: #fff;
        border: none;
        border-radius: 16px;
        padding: 0 10px;
        font-size: 27px;
        line-height: 120%;
        color: #000;
        &::placeholder {
          color: #363535;
          opacity: 0.5;
        }
      }
    }
    &-btn {
      width: 50%;
    }
  }
}
.installment {
  padding: 20px 0;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    border-radius: 35px;
    padding: 0 30px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 100%;
      height: calc(100% - 180px);
      background-image: url("../img/installment-bg.png");
      background-repeat: no-repeat;
      background-position: center;
      border-radius: 35px;
      background-size: cover;
      pointer-events: none;
    }
  }
  &__left {
    position: relative;
    z-index: 2;
    max-width: 608px;
    width: 55%;
  }
  &__right {
    position: relative;
    z-index: 2;
    width: 45%;
    padding: 110px 0;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 77px;
    line-height: 100%;
    color: #fff;
  }
  &__subtitle {
    display: flex;
    font-size: 58px;
    line-height: 105%;
    font-weight: 300;
    color: #000;
    margin-top: 30px;
    span {
      font-size: 54px;
      background: #fff;
      border-radius: 14px;
      padding: 0 5px;
      margin-left: 8px;
    }
  }
  &__btn {
    margin-top: 20px;
  }
}
.news {
  padding: 20px 0;
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  .swiper {
    overflow: unset;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-active,
    &-active + .swiper-slide {
      opacity: 1;
      pointer-events: auto;
    }
  }
  .swiper-pagination {
    position: static;
    margin-top: 60px;
  }
}
.news-card {
  height: auto;
  display: flex;
  gap: 15px;
  background-color: #ffffff;
  filter: drop-shadow(1.805px 2.396px 3.5px rgba(0,0,0,0.35));
  border-radius: 20px;
  padding: 24px;
  &__img {
    width: 50%;
    img {
      width: 100%;
    }
  }
  &__desc {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    p {
      font-size: 21px;
      line-height: 130%;
      color: #494EB4;
    }
  }
  &__logo {
    max-width: 184px;
    margin-top: 15px;
    img {
      width: 100%;
    }
  }
}
.partners {
  padding: 20px 0;
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-width: 100%;
    }
  }
  .swiper-button-prev {
    left: -30px;
  }
  .swiper-button-next {
    right: -30px;
  }
  .swiper-pagination {
    position: static;
    margin-top: 15px;
    display: none;
  }
}
.feedback {
  padding: 20px 0 60px;
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-pagination {
    position: static;
    margin-top: 60px;
  }
}
.feedback-card {
  height: auto;
  border: 3px solid #fcc105;
  border-radius: 30px;
  padding: 20px;
  &__user {
    display: flex;
    align-items: center;
    gap: 12px;
  }
  &__user-img {
    width: 125px;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__user-name {
    font-size: 20px;
    line-height: 130%;
    font-weight: 700;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 15px;
    p {
      font-size: 17px;
      line-height: 130%;
    }
  }
}
.social {
  position: relative;
  z-index: 1;
  padding: 60px 0 0;
  &__inner {
    position: relative;
    background-image: url("../img/social-bg.png");
    background-repeat: no-repeat;
    background-position: left bottom;
    background-size: cover;
    border-radius: 35px;
    padding: 100px;
  }
  &__wrap {
    max-width: 520px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-left: auto;
  }
  &__title {
    font-size: 41px;
    line-height: 130%;
    font-weight: 700;
    color: #fff;
  }
  &__items {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-top: 30px;
  }
  &__item {
    width: 160px;
    &:hover {
      opacity: 0.7;
    }
    img {
      width: 100%;
    }
  }
  &__img {
    position: absolute;
    top: -80px;
    right: 200px;
    width: 337px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.contacts {
  &__inner {
    position: relative;
    padding: 120px 0 60px 0;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    gap: 40px;
    margin-top: 30px;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__group2 {
    display: flex;
    gap: 40px;
    p {
      font-size: 17px;
      line-height: 130%;
      font-weight: 500;
    }
    a {
      display: block;
      font-size: 17px;
      line-height: 130%;
    }
    a:hover {
      color: #000795;
    }
  }
  &__logo {
    width: 110px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__logo2 {
    width: 257px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__subtitle {
    text-align: center;
    font-size: 34px;
    line-height: 130%;
    font-weight: 500;
    color: #000795;
    margin-top: 60px;
  }
  &__img {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.map {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 440px;
  &-wrap {
    width: 100%;
    height: 100%;
  }
  iframe {
    width: 100%;
    height: 100%;
    border: none;
  }
}


@media screen and (max-width: 1350px) {
  .partners {
    &__slider {
      padding: 0 35px;
    }
    .swiper-button-prev {
      left: 0;
    }
    .swiper-button-next {
      right: 0;
    }
  }
}

@media screen and (max-width: 1200px) {
  .connection {
    &__inner {
      gap: 30px;
    }
    &__left {
      max-width: 350px;
    }
    &__right {
      width: calc(100% - 380px);
    }
    &__text {
      font-size: 20px;
    }
    .form {
      gap: 24px;
      &-group {
        input {
          font-size: 20px;
        }
      }
    }
  }
  .installment {
    &__title {
      font-size: 50px;
    }
    &__subtitle {
      font-size: 40px;
      span {
        font-size: 40px;
      }
    }
  }
  .news-card {
    &__text {
      p {
        font-size: 18px;
      }
    }
  }
  .feedback-card {
    &__user {
      &-img {
        width: 100px;
      }
      &-name {
        font-size: 18px;
      }
    }
    &__text {
      p {
        font-size: 16px;
      }
    }
  }
  .social {
    &__wrap {
      max-width: 460px;
    }
    &__title {
      font-size: 32px;
    }
    &__item {
      width: 120px;
    }
  }
  .contacts {
    &__inner {
      gap: 24px;
    }
    &__logo {
      width: 90px;
    }
    &__logo2 {
      width: 200px;
    }
    &__group2 {
      gap: 24px;
      p {
        font-size: 16px;
      }
      a {
        font-size: 16px;
      }
    }
    &__subtitle {
      font-size: 32px;
    }
  }
}

@media screen and (max-width: 992px) {
  .section-group {
    padding: 15px 0;
    &__inner {
      border-width: 6px;
    }
  }
  .selection {
    padding: 30px;
  }
  .services {
    padding: 30px;
  }
  .connection {
    padding: 15px 0;
    &__inner {
      gap: 16px;
      padding: 30px;
    }
    &__left {
      max-width: 300px;
    }
    &__right {
      width: calc(100% - 316px);
    }
    &__text {
      font-size: 18px;
    }
    .form {
      gap: 16px;
      &-group {
        input {
          height: 60px;
          font-size: 18px;
        }
      }
    }
  }
  .installment {
    padding: 15px 0;
    &__title {
      font-size: 40px;
    }
    &__subtitle {
      font-size: 24px;
      margin-top: 20px;
      span {
        font-size: 24px;
        border-radius: 6px;
      }
    }
  }
  .news {
    padding: 15px 0;
    .swiper-pagination {
      margin-top: 30px;
    }
  }
  .news-card {
    padding: 16px;
    &__text {
      p {
        font-size: 16px;
      }
    }
  }
  .partners {
    padding: 15px 0;
  }
  .feedback {
    padding: 15px 0 30px;
    .swiper-pagination {
      margin-top: 30px;
    }
  }
  .social {
    padding: 30px 0 0 0;
    &__inner {
      padding: 100px 50px;
    }
    &__wrap {
      max-width: 400px;
    }
    &__title {
      font-size: 24px;
    }
    &__items {
      margin-top: 15px;
    }
    &__item {
      width: 100px;
    }
    &__img {
      top: -40px;
      width: 260px;
    }
  }
  .contacts {
    &__inner {
      padding: 60px 0;
    }
    &__wrap {
      gap: 16px;
    }
    &__group {
      gap: 16px;
    }
    &__group2 {
      gap: 16px;
      p {
        font-size: 14px;
      }
      a {
        font-size: 14px;
      }
    }
    &__logo {
      width: 70px;
    }
    &__logo2 {
      width: 160px;
    }
    &__subtitle {
      font-size: 24px;
      margin-top: 30px;
    }
  }
  .map {
    height: 300px;
  }
}

@media screen and (max-width: 768px) {
  .section-group {
    &__inner {
      border-width: 3px;
      border-radius: 16px;
    }
  }
  .selection {
    padding: 24px;
  }
  .services {
    padding: 24px;
  }
  .connection {
    &__inner {
      flex-direction: column;
      align-items: start;
      border-radius: 16px;
      padding: 24px;
    }
    &__left {
      max-width: 100%;
    }
    &__right {
      width: 100%;
    }
  }
  .installment {
    &__inner {
      border-radius: 16px;
      padding: 0 24px;
      &:after {
        height: calc(100% - 120px);
        border-radius: 16px;
      }
    }
    &__right {
      padding: 80px 0;
    }
    &__title {
      font-size: 32px;
    }
    &__subtitle {
      font-size: 18px;
      span {
        font-size: 18px;
      }
    }
  }
  .news-card {
    &__text {
      p {
        font-size: 14px;
      }
    }
    &__logo {
      max-width: 100px;
    }
  }
  .feedback-card {
    padding: 16px;
    &__user {
      &-img {
        width: 80px;
      }
      &-name {
        font-size: 16px;
      }
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
  }
  .social {
    &__inner {
      border-radius: 16px;
      padding: 100px 24px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.3);
        border-radius: 16px;
        pointer-events: none;
      }
    }
    &__wrap {
      position: relative;
      z-index: 1;
    }
    &__img {
      z-index: 1;
    }
  }
  .contacts {
    &__wrap {
      flex-direction: column;
    }
    &__img {
      top: 50%;
    }
  }
}

@media screen and (max-width: 576px) {
  .selection {
    padding: 16px;
  }
  .services {
    padding: 16px;
  }
  .connection {
    &__inner {
      padding: 16px;
    }
    &__text {
      font-size: 16px;
    }
    .form {
      &-group {
        input {
          height: 50px;
          font-size: 14px;
        }
      }
    }
  }
  .installment {
    &__inner {
      flex-direction: column;
      text-align: center;
      gap: 16px;
      padding: 16px;
      &:after {
        height: 100%;
      }
    }
    &__left {
      max-width: 300px;
      width: 100%;
    }
    &__right {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 0;
    }
    &__subtitle {
      justify-content: center;
    }
  }
  .news {
    &__slider {
      margin-top: 15px;
    }
    .swiper-pagination {
      margin-top: 15px;
    }
  }
  .partners {
    &__slider {
      padding: 0;
      margin-top: 15px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }
    .swiper-pagination {
      display: block;
    }
  }
  .feedback {
    &__slider {
      max-width: 300px;
      margin: 15px auto 0;
    }
    .swiper-pagination {
      margin-top: 15px;
    }
  }
  .social {
    &__inner {
      padding: 60px 16px;
    }
    &__wrap {
      max-width: 100%;
      align-items: center;
    }
    &__item {
      width: 60px;
    }
    &__img {
      right: 50%;
      transform: translate(50%, 0);
      width: 200px;
    }
  }
  .contacts {
    &__inner {
      padding: 30px 0;
    }
    &__wrap {
      margin-top: 15px;
    }
    &__group2 {
      flex-direction: column;
    }
    &__subtitle {
      margin-top: 15px;
    }
    &__img {
      height: auto;
    }
  }
}

@media screen and (max-width: 400px) {
  .connection {
    .form {
      flex-direction: column;
      gap: 8px;
      &-group {
        width: 100%;
      }
      &-btn {
        width: 100%;
      }
    }
  }
}