.header-top {
  position: relative;
  z-index: 2;
  background: #fff;
  padding: 15px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
  }
  &__left {
    display: flex;
    gap: 24px;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__logo {
    width: 110px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__logo2 {
    width: 257px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__group {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 24px;
  }
  &__group2 {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__socials {
    display: flex;
    gap: 24px;
  }
  &__social {
    max-width: 32px;
    &:hover {
      opacity: 0.7;
    }
    img {
      max-width: 100%;
    }
  }
  &__selects {
    display: flex;
    align-items: stretch;
    gap: 16px;
  }
  .select__wrap {
    position: relative;
  }
  .select__wrap--active {
    .select__list {
      transform: translate(0, 0);
      opacity: 1;
      pointer-events: auto;
    }
  }
  .select__list-default {
    position: relative;
    z-index: 2;
    height: 100%;
    display: flex;
    align-items: center;
    gap: 5px;
    background: #fff;
    border: 1px solid #000795;
    border-radius: 10px;
    padding: 2px 10px;
    cursor: pointer;
  }
  .select__current {
    font-size: 24px;
    line-height: 130%;
    color: #000795;
  }
  .select-icon {
    max-width: 37px;
    svg,
    img {
      max-width: 100%;
      height: auto;
    }
    svg path {
      fill: RGBA(255, 126, 22, 1);
    }
  }
  .select-arrow {
    width: 11px;
    img {
      width: 100%;
    }
  }
  .select__list {
    position: absolute;
    top: 100%;
    left: 0;
    transform: translate(0, -10px);
    width: 100%;
    background: #fff;
    border: 1px solid #000795;
    border-radius: 10px;
    padding: 5px 10px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    li {
      font-size: 24px;
      line-height: 130%;
      color: #000795;
      cursor: pointer;
    }
    .li--active {
      font-weight: 700;
    }
  }
  &__location {
    font-size: 18px;
    line-height: 130%;
    text-align: right;
  }
  &__phone {
    font-size: 18px;
    line-height: 130%;
    margin-top: 5px;
    &:hover {
      color: #000795;
    }
  }
}
.header-bottom {
  background: rgba(104, 185, 0, 1);
  color: #fff;
  padding: 32px 0;
  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
    gap: 10px 25px;
    li a {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #ff7e16;
      filter: drop-shadow(4.815px 6.389px 0.5px rgba(0,0,0,0.28));
      border-radius: 11px;
      padding: 10px;
      font-size: 24px;
      line-height: 130%;
    }
    li a:hover {
      background: #fff;
      color: #000795;
    }
    .active a {
      background: #fff;
      color: #000795;
    }
  }
  .header-top__socials {
    display: none;
  }
  .language-content {
    display: none;
  }
  .language-content--active {
    display: flex;
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 32px;
  height: 22px;
  flex-shrink: 0;
  display: none;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #000795;
    border-radius: 3px;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 10px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 10px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1200px) {
  .header-bottom {
    ul {
      justify-content: start;
      gap: 15px;
      li a {
        font-size: 22px;
      }
    }
  }
}

@media screen and (max-width: 1150px) {
  .header-top {
    &__logo {
      width: 90px;
    }
    &__logo2 {
      width: 200px;
    }
    &__location {
      font-size: 16px;
    }
    &__phone {
      font-size: 16px;
    }
    .select__current {
      font-size: 18px;
    }
    .select__list {
      li {
        font-size: 18px;
      }
    }
  }
  .header-bottom {
    padding: 24px 0;
    ul {
      li a {
        font-size: 18px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header-top {
    z-index: unset;
    &__left {
      gap: 16px;
    }
    &__logo {
      width: 70px;
    }
    &__logo2 {
      width: 160px;
    }
    &__socials {
      display: none;
    }
    .select-icon {
      max-width: 24px;
    }
    .select__current {
      font-size: 16px;
    }
    .select__list {
      li {
        font-size: 16px;
      }
    }
    &__location,
    &__phone {
      font-size: 14px;
    }
  }
  .header-bottom {
    position: fixed;
    z-index: 10;
    top: 0;
    left: -105%;
    width: 100%;
    height: 100%;
    padding: 120px 15px 50px;
    transition: all 0.3s;
    pointer-events: none;
    overflow-y: auto;
    &--active {
      left: 0;
      pointer-events: auto;
    }
    &__inner {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    ul {
      display: inline-flex;
      flex-direction: column;
      li a {
        background: transparent !important;
        filter: unset !important;
        padding: 0;
      }
    }
    .header-top__socials {
      display: flex;
      margin-top: 30px;
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 768px) {
  .header-top {
    &__logo2 {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .header-top {
    &__inner {
      gap: 16px;
    }
    &__right {
      gap: 16px;
    }
    &__socials {
      gap: 16px;
    }
    &__social {
      max-width: 24px;
    }
    &__selects {
      display: block;
    }
    .select__wrap {
      display: inline-block;
      &:nth-child(1) {
        z-index: 3;
        display: block;
        margin-bottom: 8px;
      }
    }
    .select__list-default {
      padding: 5px 7px;
    }
    .select-icon {
      max-width: 20px;
    }
    .select__current {
      font-size: 14px;
    }
    .select-arrow {
      width: 8px;
    }
    .select__list {
      li {
        font-size: 14px;
      }
    }
    &__location,
    &__phone {
      font-size: 12px;
    }
  }
  .header-bottom {
    ul {
      li a {
        font-size: 14px;
      }
    }
  }
  .burger {
    width: 26px;
    height: 16px;
    &--active {
      span:nth-child(1) {
        top: 7px;
      }
      span:nth-child(3) {
        bottom: 7px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .header-top {
    &__inner {
      gap: 8px;
    }
    &__right {
      gap: 8px;
    }
    .select-icon {
      max-width: 16px;
    }
    .select__current {
      font-size: 12px;
    }
    .select-arrow {
      width: 6px;
    }
    .select__list {
      li {
        font-size: 12px;
      }
    }
  }
}

@media screen and (max-width: 365px) {
  .header-top {
    &__logo {
      width: 55px;
    }
    &__location,
    &__phone {
      font-size: 10px;
    }
  }
}