.visas {
  padding: 60px 0 230px;
  h2 {
    max-width: 600px;
  }
  &__group {
    position: relative;
    background-image: url("../img/visas-bg.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 35px;
    padding: 40px;
    margin-top: 30px;
  }
  &__text {
    max-width: 660px;
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    p {
      font-size: 17px;
      line-height: 130%;
      font-weight: 300;
    }
  }
  &__img {
    position: absolute;
    right: 50px;
    bottom: 0;
    max-width: 390px;
    width: 100%;
    img {
      width: 100%;
      display: block;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
  }
  &__item {
    border: 1px solid #cdcdcd;
    border-radius: 26px;
    &--active {
      .arrow {
        transform: rotate(-180deg);
      }
      .file {
        display: flex !important;
      }
    }
    &-show {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      gap: 15px;
      padding: 24px;
      cursor: pointer;
    }
    &-title {
      display: flex;
      align-items: center;
      gap: 15px;
      font-size: 30px;
      line-height: 130%;
    }
    &-hidden {
      display: none;
      padding: 0 24px 30px 126px;
    }
    nav {
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
    &-subtitle {
      font-size: 17px;
      line-height: 130%;
      font-weight: 500;
      margin-bottom: 15px;
    }
    ul {
      display: grid;
      grid-template-columns: 100%;
      li {
        font-size: 17px;
        line-height: 130%;
        list-style-type: decimal;
        list-style-position: inside;
      }
    }
    &-text {
      margin-top: 30px;
      p {
        font-size: 17px;
        line-height: 130%;
      }
    }
  }
  .flag {
    width: 87px;
    img {
      width: 100%;
    }
  }
  .arrow {
    width: 30px;
    display: flex;
    align-items: center;
    transition: all 0.3s;
    img {
      width: 100%;
    }
  }
  .file {
    display: none;
    align-items: center;
    gap: 15px;
    i {
      width: 54px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 17px;
      line-height: 130%;
      font-weight: 500;
    }
  }
}

@media screen and (max-width: 1200px) {
  .visas {
    &__text {
      max-width: 550px;
      p {
        font-size: 16px;
      }
    }
    &__item {
      &-title {
        gap: 10px;
        font-size: 24px;
      }
      &-hidden {
        padding: 0 24px 24px 24px;
      }
      &-subtitle {
        font-size: 16px;
      }
      ul {
        li {
          font-size: 16px;
        }
      }
      &-text {
        p {
          font-size: 16px;
        }
      }
    }
    .flag {
      width:60px;
    }
    .arrow {
      width: 24px;
    }
    .file {
      gap: 10px;
      i {
        width: 40px;
      }
      span {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .visas {
    &__text {
      max-width: 500px;
    }
    &__img {
      max-width: 350px;
    }
  }
}

@media screen and (max-width: 992px) {
  .visas {
    padding: 30px 0 60px;
    &__text {
      max-width: 420px;
    }
    &__img {
      width: 36vw;
    }
    &__item {
      &-subtitle {
        font-size: 14px;
      }
      ul {
        li {
          font-size: 14px;
        }
      }
      &-text {
        p {
          font-size: 14px;
        }
      }
    }
  }
}

@media screen and (max-width: 850px) {
  .visas {
    &__text {
      max-width: 350px;
    }
  }
}

@media screen and (max-width: 768px) {
  .visas {
    &__group {
      border-radius: 16px;
      padding: 24px 24px 0 24px;
    }
    &__text {
      max-width: 100%;
    }
    &__img {
      position: static;
      max-width: 300px;
      width: 100%;
      margin: 16px auto 0;
    }
    &__item {
      border-radius: 16px;
    }
  }
}

@media screen and (max-width: 576px) {
  .visas {
    &__group {
      padding: 16px 16px 0 16px;
      margin-top: 15px;
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
    &__item {
      &-show {
        padding: 16px;
      }
      &-title {
        font-size: 18px;
      }
      &-hidden {
        padding: 0 16px 16px 16px;
      }
    }
    .flag {
      width: 40px;
    }
    .file {
      i {
        width: 30px;
      }
      span {
        font-size: 16px;
      }
    }
  }
}